import './App.scss';
import FormCreated from './components/FormCreated';

function App() {
  return (
    <div className="App">
      <FormCreated
        color={'red'}
        realFormName={'  '}
        switches={[
          { label: '', checked: true, name: 'name' },
          { label: '', checked: true, name: 'tel' },
          { label: '', checked: true, name: 'city' },
          { label: '', checked: true, name: 'subject' },
          { label: ' ', checked: true, name: 'font' },
          {
            label: '   ',
            checked: true,
            name: 'commFile',
          },
        ]}
        subjectName={{
          value: '  ',
          label: '  ',
        }}
        cityName={'  '}
        buttonName={' '}
        workType={{ value: undefined, label: ' ' }}
      />
    </div>
  );
}

export default App;
